:root {
  --main-color: #000;
  --accent-color: #00acdc;
  --main-font-style: "Roboto", sans-serif;
  --main-font-size: 16px;
  --logo-width: 40px;
  --logo-height: 40px;
  --header-height: 60px;
}

html {
  box-sizing: border-box;
  color: var(--main-color);
  font-family: var(--main-font-style);
  font-size: var(--main-font-size);
}

body {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

li {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__link {
  width: var(--logo-width);
  height: var(--logo-height);
  display: inline-block;
}

.header__logo {
  width: 100%;
  height: auto;
  display: block;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: inherit;
  font-weight: 500;
  font-size: calc(var(--main-font-size)  - 4px);
  text-transform: uppercase;
  min-height: var(--header-height);
  justify-content: center;
  align-items: center;
  line-height: 1.17;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link.is-active, .nav__link:hover {
  color: var(--accent-color);
}

.is-active:after {
  content: "";
  background-color: var(--accent-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}
